.CG2-compactNav{
  font-family: $fontSansSerif;
  font-size: 1rem;
  vertical-align: bottom;
  @include max-screen( $breakpoint-base ) {
    font-size: 0.9rem;
  }
  @include max-screen( $breakpoint-middle ) {
    line-height: 2.2em;
    vertical-align: middle;
    position: relative;
    z-index: $z-flyout;
    display: inline-block;
    width: 120px;
    padding-right: 30px;
    border: 1px solid #dbdbdb;
    border-radius: 3px;
    height: 2.2em;
  }
  &.CG2-compactNav--show{}
  ul{
    list-style: none;
    table-layout: fixed;
    display: table;
    padding: 0;
    margin: 0 auto;
    @include max-screen( $breakpoint-middle ) {
      position: absolute;
      display: block;
      width: 120px;
      border: 1px solid #dbdbdb;
      border-radius: 3px;
      margin: -1px;
      background: #fff;
    }
  }
    li{
      font-family: $fontSerif;
      text-align: center;
      vertical-align: middle;
      display: table-cell;
      padding: 2px 40px;
      border-left: 1px solid #dbdbdb;
      margin: 0;
      &:first-child{
        border-left: none;
      }
      @include max-screen( $breakpoint-base ) {
        padding-left: 20px;
        padding-right: 20px;
      }
      @include max-screen( $breakpoint-middle ) {
        font-family: $fontSansSerif;
        display: block;
        text-align: left;
        display: none;
        border-left: none;
        padding: 0 10px;
        @at-root .CG2-compactNav.CG2-compactNav--show li{
          display: block;
        }
      }
      &.CG2-compactNav__item--current{
        color: #fff;
        display: block;
        background: #282828;
        @include max-screen( $breakpoint-middle ) {
          color: inherit;
          background: rgba( 0, 0, 0, 0.03 );
        }
      }
    }
      a{
        @include resetLink();
        text-decoration: none;
        position: relative;
        display: block;
        padding: 2px 40px;
        margin: -2px -40px;
        @include max-screen( $breakpoint-base ) {
          padding-left: 20px;
          padding-right: 20px;
          margin-left: -20px;
          margin-right: -20px;
        }
        @include max-screen( $breakpoint-middle ) {
          padding: 0 10px;
          margin: 0 -10px;
        }
      }
    li:not( .CG2-compactNav__item--current ) a{
      &:after{
        content: '';
        position: absolute;
        left: 50%;
        right: 50%;
        bottom: 0;
        display: block;
        height: 2px;
        background: #424242;
        transition: all .4s cubic-bezier(.3, 1.4, .6, 1 );
      }
      &:hover:after,
      &:focus:after{
        left: 0;
        right: 0;
      }
      @include max-screen( $breakpoint-middle ) {
        &:after{
          content: none;
        }
      }
    }
  .CG2-compactNav__navOpener{
    @include max-screen( $breakpoint-middle ) {
      line-height: 2.2em;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: 30px;
      height: 2.2em;
      background: #ebebeb;
      cursor: pointer;
      &:after{
        @include icon( 'hamburger' );
        font-size: 14px;
        line-height: 0;
        position: absolute;
        top: 50%;
        left: 0;
        width: 30px;
        text-align: center;
      }
    }
  }
}


