.CG2-articleHeader{
  @include blurry-parent();
  color: #fff;
}
  .CG2-articleHeader__inner{
    position: relative;
    z-index: 1;
    box-sizing: border-box;
    max-width: 1016px;
    padding: 40px 190px;
    margin: 0 auto;
    @include max-screen( $breakpoint-base ) {
      padding-left: ( 190 / 1016 ) * 100%;
      padding-right: ( 190 / 1016 ) * 100%;
    }
    @include max-screen( $breakpoint-middle ) {
      width: auto;
      max-width: 656px;
      padding: 20px;
    }
  }


  .CG2-articleHeader__category{
    position: absolute;
    top: calc( 40px + 0.5em );
    left: 50px;
    width: 50px;
    @include max-screen( $breakpoint-middle ) {
      vertical-align: middle;
      position: static;
      display: table;
      width: auto;
      margin-bottom: 10px;
    }
  }
    .CG2-articleHeader__categoryIcon{
      width: 50px;
      height: 50px;
      margin-bottom: 10px;
      span[class*="CG2-icon-cat"]{
        font-size: 50px;
        line-height: 50px;
        top: 0;
      }
      @include max-screen( $breakpoint-middle ) {
        display: table-cell;
        width: auto;
        height: auto;
        span[class*="CG2-icon-cat"]{
          font-size: 32px;
          line-height: 32px;
        }
      }
    }
    .CG2-articleHeader__categoryName{
      letter-spacing: 0.2em;
      line-height: 50px;
      -webkit-writing-mode: vertical-rl;
      writing-mode: tb-rl; // IE
      writing-mode: vertical-rl;
      margin: auto;
      @include max-screen( $breakpoint-middle ) {
        font-size: 12px;
        letter-spacing: 0;
        line-height: normal;
        vertical-align: middle;
        -webkit-writing-mode: inherit;
        writing-mode: inherit; // IE
        writing-mode: inherit;
        display: table-cell;
        padding-left: 10px;
      }
    }

  .CG2-articleHeader__main{
    display: table;
    width: 100%;
    height: 260px;
    @include max-screen( $breakpoint-middle ) {
      height: auto;
    }
  }
    .CG2-articleHeader__mainInner{
      display: table-cell;
      vertical-align: top;
    }

      .CG2-articleHeader__series{
        font-size: 24px;
        font-family: $fontSerif;
        line-height: 1.4;
        a{
          color: inherit;
          text-decoration: inherit;
          display: inline-block;
          position: relative;
          &:after{
            content: '';
            opacity: 0;
            position: absolute;
            bottom: -3px;
            left: 0;
            right: 0;
            display: block;
            height: 1px;
            background: #fff;
            transition: all .6s;
          }
          &:hover:after,
          &:focus:after{
            bottom: 0;
            opacity: 1;
          }
        }
      }
      .CG2-articleHeader__title{
        font-size: 32px;
        font-family: $fontSerif;
        font-weight: 600;
        line-height: 1.4;
        margin-bottom: 22px;
        @include max-screen( $breakpoint-middle ) {
          font-size: 18px;
        }
      }
      .CG2-articleHeader__abstract{
        line-height: 1.6;
        p{
          margin-top: .5em;
          margin-bottom: .5em;
        }
        @include max-screen( $breakpoint-middle ) {
          font-size: 12px;
        }
      }
      .CG2-articleHeader__pubDate{
        font-size: 12px;
        opacity: 0.5;
      }

      .CG2-articleHeader__authors{
        font-size: 0;
        margin: 10px 0;
      }
        .CG2-articleHeader__author{
          font-size: 1rem;
          display: inline-table;
          margin: 3px 10px 3px 0;
          margin-right: 10px;
          & > a {
            color: inherit;
            text-decoration: none;
            position: relative;
            display: table;
            width: 100%;
            &:before{
              content: '';
              display: block;
              position: absolute;
              top: -2px;
              right: -2px;
              bottom: -2px;
              left: -2px;
              background: rgba( 255, 255, 255, 0 );
              transition: all .5s;
            }
            &:hover:before,
            &:focus:before {
              background: rgba( 255, 255, 255, 0.2 );
              transition: all .2s;
            }
          }
        }
          .CG2-articleHeader__authorImage{
            vertical-align: middle;
            display: table-cell;
            padding-right: 8px;
            img{
              display: block;
              width: 24px;
              height: 24px;
            }
          }
          .CG2-articleHeader__authorName{
            font-size: 12px;
            vertical-align: middle;
            display: table-cell;
            padding-right: 6px;
          }
          .CG2-articleHeader__authorTitle{
            font-size: 12px;
            vertical-align: middle;
            display: table-cell;
            padding-left: 6px;
            position: relative;
            &:before{
              content: '';
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              height: 1.4em;
              border-left: 1px solid #fff;
              margin: auto;
            }
          }

  .CG2-articleHeader__pagenation{
    padding-top: 20px;
    margin: 0 -140px;
    @include max-screen( $breakpoint-middle ) {
      display: none;
    }
    ul{
      list-style: none;
      padding: 0;
      margin: 0;
    }
      li{
        vertical-align: middle;
        box-sizing: border-box;
        display: inline-block;
        margin: 0;
        a{
          color: inherit;
          font-size: 14px;
          line-height: 1.2;
          text-decoration: none;
          position: relative;
          display: inline-block;
          padding: 6px 8px;
          border: 1px solid #fff;
          border-radius: 2px;
          background: rgba( 255, 255, 255, 0 );
          transition: all .3s;
          @include max-screen( $breakpoint-middle ) {
            font-size: 12px;
            padding: 4px 6px;
          }
          &:hover,
          &:focus{
            background: rgba( 255, 255, 255, 0.2 );
          }
          &:before{
            position: absolute;
            top: 50%;
            transition: all .2s;
          }
        }
        &.CG2-articleHeader__pagenationPrev{
          float: left;
          a{
            padding-right: 12px;
            padding-left: calc( 1.2em + 8px );
            &:before{
              @include icon( 'left' );
              line-height: 0;
              left: 8px;
            }
            &:hover:before,
            &:focus:before{
              left: 5px;
            }
          }
        }
        &.CG2-articleHeader__pagenationNext{
          float: right;
          a{
            padding-right: calc( 1.2em + 8px );
            padding-left: 12px;
            &:before{
              @include icon( 'right' );
              line-height: 0;
              right: 8px;
            }
            &:hover:before,
            &:focus:before{
              right: 5px;
            }
          }
        }
      }
  }
  .CG2-articleHeader__utils{
    @include max-screen( $breakpoint-middle ) {
      margin-top: 10px;
    }
    ul{
      font-size: 0;
      list-style: none;
      padding: 0;
      margin: 0;
    }
    li{
      font-size: 1rem;
      display: inline-block;
      margin: 0 10px 10px 0;
      @include max-screen( $breakpoint-middle ) {
        margin: 0 5px 5px 0;
      }
    }
  }
  .CG2-articleHeader__utilButton{}

  .CG2-articleHeader__bg{
    @include blurry();
    // &.CG2-articleHeader__bg--blurry{
    //   @include blurry--blurry();
    // }
  }



@include print () {

.CG2-articleHeader{
  color: #000;
  background: #fff;
  border-bottom: 1px solid #000;
}
  .CG2-articleHeader__inner{
    width: auto;
    padding: 20px 190px;
  }
  .CG2-articleHeader__pagenation{
    display: none;
  }
  .CG2-articleHeader__bg{
    display: none;
  }

}



