            display: block;
            border-left: none;
            padding: 8px 16px;
            border-top: 1px solid #dbdbdb;
            &:first-child{
              border-top: none;
            }
          }
        }
          a{
            @include resetLink();
            text-decoration: none;
            display: block;
            padding: 10px;
            margin: -10px;
            transition: background-color .2s;
            &[data-livecode-play]{
              position: relative;
              &:before{
                position: absolute;
                content: '';
                background: #eee;
                transition: background-color .2s;
                @at-root .CG2-livecode.CG2-livecode--isRunning &{
                  content: '';
                }
              }
            }
            &:hover, &:hover::before{
              background-color: #dbdbdb;
            }
            span[class^="CG2-icon-"]{
              font-size: 20px;
              position: static;
              margin: 0 5px 0 0;
            }
          }
    }
  }
  .CG2-livecode__body{
    -webkit-overflow-scrolling:touch;
    overflow: auto;
    position: relative;
    border: 1px solid #dbdbdb;
    iframe{
      vertical-align: bottom;
      resize: vertical;
      display: block;
      box-sizing: border-box;
      width: 100%;
      max-width: 100%;
      min-height: 390px;
      border: none;
      background: #fff;
      &.CG2-livecode__frame--small{min-height: 190px;}
      &.CG2-livecode__frame--large{min-height: 590px;}
    }
    pre[class*="language-"] {
      margin: 0;
      resize: vertical;
      code{
        padding: 0;
        background: transparent;
      }
    }
  }
  .CG2-livecode__clickToPlay{
    overflow: hidden;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    background: #777;
    transition: all .3s;
    @at-root .CG2-livecode.CG2-livecode--isRunning &{
      bottom: 100%;
    }
    &:after{
      content: '';
      color: #fff;
      font-size: 18px;
      text-align: center;
      display: block;
      width: 100%;
    }
  }
  // iPhoneiframe
  // iPhoneJavaScriptiframe
  .CG2-livecode__body--inner {
    height: 40vh;
  }


